<template>
  <div id='app'>
    <div class='container-fluid' style="padding: 0px 0px 0px 0px">
      <Header school='东北大学'></Header>
      <div class="">
        <div class="center-block school-bar" :style='{width: sizeChange(1152)}'>
          <span class="logo" />
          <span class="name f1" v-cloak>{{schoolData.schoolName}}</span>
        </div>
      </div>
      <div class=' banner' :style='{ width: sizeChange(1920), height: sizeChange(289)}'>
        <div class='f1' :style='{ fontSize: sizeChange(48), paddingTop: sizeChange(45), marginLeft: sizeChange(644)}'>砺英网</div>
        <div class='f2' :style='{ fontSize: sizeChange(18), marginTop: sizeChange(20), marginLeft: sizeChange(644)}'>软件开发综合技能实战训练平台</div>
      </div>
      <div class=' shadow'>
        <HorizontalTap v-if="courseFlg !== ''" class='center-block' :data='yearData' :active="0" @activeItem="selectGrade" style="padding-left: 300px; width: 100%;"></HorizontalTap>
      </div>
      <div v-show="courseShow" class='center-block mt20' :style='{width: sizeChange(1152)}'>
        <div v-for="value in courseData" v-bind:key="value.grade" v-show="value.current === 'true'">
          <div class='course-box' v-show="value.list.length > 0" :style='{width: sizeChange(378)}' v-for='obj in value.list' v-bind:key="obj.courseId" @click="showProject(obj.courseId, obj.courseName)">
            <img class='logo' src='../assets/img/course1.png' :style='{ height: sizeChange(243)}'/>
            <img v-if="obj.courseStatus === '2'" class='state' :style='{ width: sizeChange(81), height: sizeChange(30), top: sizeChange(227), left: sizeChange(273)}' src='../assets/img/complete.png' />
            <img v-else-if="obj.courseStatus === '1'" class='state' :style='{ width: sizeChange(81), height: sizeChange(30), top: sizeChange(227), left: sizeChange(273)}' src='../assets/img/ongoing.png' />
            <i v-else class="iconfont iconsuo" :style='{ top: sizeChange(180), right: sizeChange(10)}'></i>
            <div class='desc'>
              <div class='pull-left'>
                <span class='title' :style='{ marginLeft: sizeChange(19)}' v-cloak>{{obj.courseName}}</span>
              </div>
            </div>
          </div>
          <div v-show="value.list.length === 0"  :style='{width: sizeChange(378)}'>
            <img src="../../../assets/img/no_data.png" class='course-box' :style='{ width: sizeChange(378), height: sizeChange(300)}'>
          </div>
        </div>
      </div>
      <div v-show="projectShow" class="center-block mt20" :style='{width: sizeChange(1152)}'>
        <div class="back-bar mb20" :style='{width: sizeChange(775)}' @click="backCourse">
          <i class="iconfont iconfanhuichangguizhuangtaizhanghuguanlijiantou"></i>
          <span v-cloak>{{activeCourseName}}</span>
        </div>
        <div class='course-box' :class="[obj.projectStatus !== '0' ? '' : 'unavailable']" :style='{width: sizeChange(378)}' v-for='obj in projectData' v-bind:key="obj.projectId" @click="goProject(obj)">
          <img class='logo' src='../assets/img/course2.png' :style='{ height: sizeChange(243)}'/>
          <img v-if="obj.projectStatus === '2'" class='state' :style='{ width: sizeChange(81), height: sizeChange(30), top: sizeChange(227), left: sizeChange(273)}' src='../assets/img/complete.png' />
          <img v-else-if="obj.projectStatus === '1'" class='state' :style='{ width: sizeChange(81), height: sizeChange(30), top: sizeChange(227), left: sizeChange(273)}' src='../assets/img/ongoing.png' />
          <i v-else class="iconfont iconsuo" :style='{ top: sizeChange(180), right: sizeChange(10)}'></i>
          <div class='desc'>
            <div class='pull-left'>
              <span class='title' :style='{ marginLeft: sizeChange(19)}' v-cloak>{{obj.projectName}}</span>
            </div>
            <div class='pull-right'>
<!--              <span class='time' :style='{ marginRight: sizeChange(29)}'>工期20天</span>-->
              <i class='iconfont iconxuexirenshu' :style='{ marginRight: sizeChange(6)}'></i>
              <span class='user-count' :style='{ marginRight: sizeChange(26)}' v-cloak>{{obj.studyCount}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="hotspot-box" :style='{width: sizeChange(400)}'>
        <img src="../../../assets/img/toupiao.png" :style='{width: sizeChange(400)}' style="border-radius: 6px">
        <div :style='{width: sizeChange(400)}' style="margin-top: 50px;">
          <span class="line"></span>
          <span class="txt">砺英推荐</span>
        </div>
        <div :style='{width: sizeChange(400)}'>
          <img src="../../../assets/img/csdn.png"  style="float: left;width: 30%;border-radius: 15px;height: 55px;">
          <a target="_blank" style="margin-left: 10px;width: 65%;float: left;margin-top: 10px" href="https://www.csdn.net/">中国专业IT社区CSDN (Chinese Software Developer Network)</a>
        </div>
        <div :style='{width: sizeChange(400)}'>
          <img src="../../../assets/img/coding.png"  style="float: left;width: 30%;border-radius: 15px;height: 55px;">
          <a target="_blank" style="margin-left: 10px;width: 65%;float: left;margin-top: 10px" >如何成为一个优秀的程序员？</a>
        </div>
        <div :style='{width: sizeChange(400)}'>
          <img src="../../../assets/img/w3c.png"  style="float: left;width: 30%;border-radius: 15px;height: 55px;">
          <a target="_blank" style="margin-left: 10px;width: 65%;float: left;margin-top: 10px" href="http://www.w3school.com.cn/">在 W3School，你可以找到你所需要的所有的网站建设教程。</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../../../components/school-training/Header.vue'
import HorizontalTap from '../../../components/tap/HorizontalTap.vue'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      winWidth: window.innerWidth,
      courseShow: true,
      projectShow: false,
      activeCourseId: '',
      activeCourseName: '',
      activeGrade: 1,
      schoolData: {},
      courseData: [],
      projectData: [],
      yearData: [{
        name: '大一',
        active: true
      }, {
        name: '大二'
      }, {
        name: '大三'
      }, {
        name: '大四'
      }],
      courseFlg: ''
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
    },
    initData: function () {
      let me = this
      this.$http.post('/campuspage/querycampusinfo', {}, res => {
        if (res.success) {
          me.schoolData.schoolName = res.data.schoolName
        } else {
          // 返回错误的处理
        }
      })
      this.$http.post('/campuspage/querycourselist', {}, res => {
        if (res.success) {
          me.courseData = res.data
          if (res.data.practice.list.length !== 0) {
            me.yearData = [{
              name: '实习生',
              active: true
            }]
            me.courseFlg = 'practice'
            me.selectGrade(4)
          } else {
            me.courseFlg = 'grade'
            me.selectGrade(0)
          }

          // console.log(me.courseData)
        } else {
          // 返回错误的处理
        }
      })
    },
    sizeChange (size) {
      return this.winWidth * size / 1920 + 'px'
    },
    selectGrade (index) {
      for (let key in this.courseData) {
        this.courseData[key].current = 'false'
      }
      if (this.courseFlg === 'grade') {
        if (index === 0) {
          this.courseData[index + 1 + 'st'].current = 'true'
        } else if (index === 1) {
          this.courseData[index + 1 + 'nd'].current = 'true'
        } else if (index === 2) {
          this.courseData[index + 1 + 'rd'].current = 'true'
        } else if (index === 3) {
          this.courseData[index + 1 + 'th'].current = 'true'
        }
      } else if (this.courseFlg === 'practice') {
        this.courseData['practice'].current = 'true'
      }

      this.activeGrade = index + 1
    },
    showProject (courseId, courseName) {
      let me = this
      this.courseShow = false
      this.projectShow = true
      this.activeCourseId = courseId
      this.activeCourseName = courseName
      this.$http.post('/campuspage/querycourseprojectlist', {
        'courseId': courseId
      }, res => {
        if (res.success) {
          // console.log(res.data)
          me.projectData = res.data
        } else {
          // 返回错误的处理
        }
      })
    },
    backCourse () {
      this.courseShow = true
      this.projectShow = false
    },
    goProject (obj) {
      localStorage.setItem('projectId', obj.projectId)
      localStorage.setItem('teamId', obj.teamId)
      localStorage.setItem('courseId', this.activeCourseId)
      localStorage.setItem('courseName', this.activeCourseName)
      localStorage.setItem('grade', this.activeGrade)
      // if (obj.projectStatus === '0') {
      //   window.location = 'team-search.html'
      // } else if (obj.projectStatus === '1') {
      //   if (obj.teamStatus === 0) {
      //     window.location = 'team-search.html'
      //   } else if (obj.teamStatus === 1 || obj.teamStatus === 2) {
      //     window.location = 'task-list.html'
      //   } else if (obj.teamStatus === 3) {
      //     window.location = 'project-task-review.html'
      //   } else {
      //     console.log(obj.teamStatus)
      //   }点评
      // }
      if (obj.projectStatus === '0') {
        // 未选择过项目
        window.location = 'course-detail.html'
      } else if (obj.projectStatus === '1') {
        if (obj.teamStatus === 0) {
          window.location = 'project-task-review.html'
        } else if (obj.teamStatus === 1 || obj.teamStatus === 2) {
          // 团队开启项目项目处于进行中状态
          window.location = 'task-list.html'
        } else if (obj.teamStatus === 3) {
          // 项目立项后未开始项目或组建完团队未开始项目
          window.location = 'project-task-review.html'
        } else {
          // console.log(obj.teamStatus)
        }
      }
    }
  }
}
</script>

<style scoped lang='scss'>
  .banner{
    background-size:100% 100%;
    background-repeat: no-repeat;
    background-image: url('../assets/img/banner1.png');
  }
  .course-box{
    position:relative;
    float:left;
    background:rgba(255,255,255,1);
    box-shadow:0px 2px 15px 0px rgba(0, 0, 0, 0.08);
    border-radius:10px;
    overflow: hidden;
    margin-bottom:20px;
    margin-right:20px;
    cursor:pointer;
    .logo{
      width:100%;
    }
    .desc{
      background-color: #FFFFFF;
      overflow:hidden;
      padding-top:13px;
      padding-bottom: 14px;
    }
    .state{
      position:absolute;
    }
    .iconsuo{
      position:absolute;
      color:#FFFFFF;
      opacity:0.5;
      font-size:24px;
    }
    .title{
      font-family:PingFang-SC-Bold;
      font-weight:bold;
      color:rgba(51,51,51,1);
    }
    .time{
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(153,153,153,1);
    }
    .user-count{
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(153,153,153,1);
    }
  }
  .unavailable{
    .logo{
      opacity:0.5;
    }
    .desc{
      background-color: #DDDDDD;
    }
    .title{
      color:#666666;
    }
    .iconxuexirenshu, .user-count{
      color:#999999;
    }
  }
  .hotspot-box{
    height: 200px;
    float:right;
    margin-right: 10%;
    .line{
      float: left;
      width: 3px;
      height: 16px;
      background: #6612cf;
      overflow: hidden;
      margin-right: 5px;
    }
    .txt {
      float: left;
      font-size:14px;
      font-family:PingFang-SC-Medium;
      font-weight:500;
      color:rgba(51,51,51,1);
      line-height: 16px;
    }
    a{
      color:#3d3d3d;
      font-weight: bold;
    }
    a:hover{
      color:#6612cf;
    }
    div{
      float: left;
      margin-bottom: 10%;
    }
  }
</style>
